@import "../mixins";
@import "../susy";

@import "../base";

#wrapper {
    @include container();
    height: 100%;
    display: flex;
    min-height: 100vh;
    flex-direction: column;
    >header {
        @include span(8);
        background-color: $color_primary;
        color: #fff;
        flex: none;
        height: 120px;
        >#prem {
            @include container();
            padding-top: 5px;
            padding-bottom: 5px;
            padding-left: 15px;
            >.titre {
                @include span(first 1);
                @media only screen and (max-width: 40rem) {
                    font-size: large;
                    padding-top: 10px;
                }
                font-size: xx-large;
                text-transform: uppercase;
                >a {
                    @include a_href();
                }
            }
            >.button {
                -moz-box-sizing: border-box;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;
                float: right;
                margin-right: 15px;
                >a {
                    font-size: xx-large;
                    color: #fff;
                    text-decoration: none;
                    >i {
                        color: #fff;
                    }
                }
            }
        }
        >#menu {
            @include container();
            >ul {
                @include span(first 6);
                @media only screen and (max-width: 40rem) {
                    display: none;
                }
                margin: 0 0 0 15px;
                padding: 0;
                list-style-type: none;
                overflow: hidden;
                >li {
                    float: left;
                    padding: 10px;
                    &.cat {
                        .dropdown-content {
                            display: none;
                            position: absolute;
                            background-color: $color_primary;
                            min-width: 160px;
                            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                            padding: 12px 16px;
                            z-index: 1;
                            a {
                                @include a_href();
                            }
                        }
                        &:hover {
                            .dropdown-content {
                                display: block;
                            }
                        }
                    }
                    >a {
                        display: inline-block;
                        @include a_href();
                    }
                }
            }
            >.mobile_menu {
                @media only screen and (min-width: 40rem) {
                    display: none;
                }
                @media only screen and (max-width: 40rem) {
                    @include span(first 6);
                    padding: 15px;
                }
            }
            >.search {
                -moz-box-sizing: border-box;
                -webkit-box-sizing: border-box;
                box-sizing: border-box;
                float: right;
                margin: 10px 15px 0 0;
                >.click_search {
                    cursor: pointer;
                    >i {
                        color: #fff;
                    }
                }
            }
        }
    }
    >#content {
        @include span(8);
        >.annonce {
            $a: span(8);
            $b: 20px;
            float: left;
            width: -webkit-calc(#{$a} - #{$b});
            width: -moz-calc(#{$a} - #{$b});
            width: calc(#{$a} - #{$b});
            color: #fff;
            margin: 10px 10px 0;
            padding: 15px;
            background-color: $color_primary;
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);  
            @include border-radius(5px);
            >a {
                @include a_href(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
            }
        }
        >.menu_right {
            @media only screen and (max-width: 40rem) {
                $a: span(8);
                $b: 10px;
                float: left;
                width: -webkit-calc(#{$a} - #{$b});
                width: -moz-calc(#{$a} - #{$b});
                width: calc(#{$a} - #{$b});
                margin: 5px;
            }
            @media only screen and (min-width: 40rem) {
                float: right;
                width: span(2);
                margin: 10px 5px 10px 0;
            }
            padding: 5px;
            >.bloc {
                padding: 5px;
                border: 1px solid rgba(0, 0, 0, 0.2);
                box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                @include border-radius(5px);
                @include after-first(1) {
                    margin-top: 10px;
                }
                &#pub {
                    @media only screen and (max-width: 40rem) {
                        display: none;
                    }
                }
                &.fixed {
                    @media only screen and (min-width: 40rem) {
                        position: fixed;
                        top: 0;
                        $a: span(2);
                        $b: 10px;
                        float: left;
                        width: -webkit-calc(#{$a} - #{$b});
                        width: -moz-calc(#{$a} - #{$b});
                        width: calc(#{$a} - #{$b});
                        right: 10px;
                    }
                }
            }
        }
        >.article_list {
            @media only screen and (max-width: 40rem) {
                float: left;
                width: span(8);
            }
            $a: span(6);
            $b: 10px;
            float: left;
            width: -webkit-calc(#{$a} - #{$b});
            width: -moz-calc(#{$a} - #{$b});
            width: calc(#{$a} - #{$b});
            padding: 5px;
            >h1 {
                color: $color_primary;
                text-align: center;
                padding: 5px;
                margin: 0;
            }
            >.nopost {
                color: $color_primary;
                text-align: center;
                padding: 5px;
                margin-top: 70px;
                font-size: x-large;
            }
        }
        >.article_last {
            $a: span(8);
            $b: 10px;
            float: left;
            width: -webkit-calc(#{$a} - #{$b});
            width: -moz-calc(#{$a} - #{$b});
            width: calc(#{$a} - #{$b});
            margin: 0 0 10px 5px;
            >h1 {
                color: $color_primary;
                text-align: center;
                padding: 5px;
                margin: 0;
            }
        }
        >.list-cat {
            margin: 5px;
            >.cat_info {
                $a: span(2);
                $b: 10px;
                float: left;
                width: -webkit-calc(#{$a} - #{$b});
                width: -moz-calc(#{$a} - #{$b});
                width: calc(#{$a} - #{$b});
                margin: 5px;
                @media only screen and (max-width: 64rem) {
                    $a: span(4);
                    $b: 10px;
                    float: left;
                    width: -webkit-calc(#{$a} - #{$b});
                    width: -moz-calc(#{$a} - #{$b});
                    width: calc(#{$a} - #{$b});
                    margin: 5px;
                }
                @media only screen and (max-width: 40rem) {
                    $a: span(8);
                    $b: 10px;
                    float: left;
                    width: -webkit-calc(#{$a} - #{$b});
                    width: -moz-calc(#{$a} - #{$b});
                    width: calc(#{$a} - #{$b});
                    margin: 5px;
                }
                >.header {
                    height: 90px;
                    background: $color_primary;
                    position: relative;
                    border: 1px solid rgba(0, 0, 0, 0.2);
                    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                    @include border-radii(5px, 5px, 0px, 0px);
                    >.icon {
                        left: 50%;
                        top: 50px;
                        margin-left: -40px;
                        position: absolute;
                        width: 80px;
                        height: 80px;
                    }
                    >.titre {
                        position: absolute;
                        top: 24px;
                        left: 0;
                        width: 100%;
                        text-align: center;
                        color: white;
                        >a {
                            color: #000;
                            text-decoration: none;
                        }
                    }
                    &:before {
                        content: '';
                        display: table;
                        clear: both;
                    }
                    &:after {
                        content: '';
                        display: table;
                        clear: both;
                    }
                }
                >.description {
                    padding: 40px 5px 5px 5px;
                    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
                    border-left: 1px solid rgba(0, 0, 0, 0.2);
                    border-right: 1px solid rgba(0, 0, 0, 0.2);
                    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                    @include border-radii(0px, 0px, 5px, 5px);
                    > .explore {
                        position: relative;
                        width: 100%;
                        text-align: center;
                        > a {
                            color: $color_primary;
                            text-decoration: none;
                        }
                    }
                }
            }
        }
        >#article {
            @media only screen and (max-width: 40rem) {
                $a: span(8);
                $b: 20px;
                float: left;
                width: -webkit-calc(#{$a} - #{$b});
                width: -moz-calc(#{$a} - #{$b});
                width: calc(#{$a} - #{$b});
                margin: 0 10px 10px;
            }
            @media only screen and (min-width: 40rem) {
                $a: span(6);
                $b: 25px;
                float: left;
                width: -webkit-calc(#{$a} - #{$b});
                width: -moz-calc(#{$a} - #{$b});
                width: calc(#{$a} - #{$b});
                margin: 15px 0 20px 10px;
            }
            >.title {
                background: #fff;
                padding: 15px 30px;
                border: 1px solid rgba(0, 0, 0, 0.2);
                box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                @include border-radii(5px, 5px, 0px, 0px);
                >h2 {
                    text-align: center;
                    font-size: 38px;
                    font-weight: 300;
                    line-height: 1.42;
                    margin-bottom: 14.5px;
                }
                >ul {
                    text-align: center;
                    margin: 0 0 20px;
                    padding: 0;
                    list-style-type: none;
                    >li {
                        display: inline-block;
                        margin-right: 5px;
                        font-size: 14px;
                        a {
                            text-decoration: none;
                            color: $color_primary;
                            &:hover, &:active {
                                color: $color_secondary;
                            }
                        }
                    }
                }
            }
            >.image {
                height: 430px;
                >img {
                    width: 100%;
                    height: 430px;
                }
            }
            >.content {
                padding: 20px;
                border: 1px solid rgba(0, 0, 0, 0.2);
                box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                @include border-radii(0px, 0px, 5px, 5px);
                >.code-toolbar {
                    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                }
            }
        }
        >.commentaire {
            @media only screen and (max-width: 40rem) {
                $a: span(8);
                $b: 20px;
                float: left;
                width: -webkit-calc(#{$a} - #{$b});
                width: -moz-calc(#{$a} - #{$b});
                width: calc(#{$a} - #{$b});
                margin: 0 10px 10px;
            }
            @media only screen and (min-width: 40rem) {
                $a: span(6);
                $b: 25px;
                float: left;
                width: -webkit-calc(#{$a} - #{$b});
                width: -moz-calc(#{$a} - #{$b});
                width: calc(#{$a} - #{$b});
                margin: 0 0 20px 10px;
            }
            border: 1px solid rgba(0, 0, 0, 0.2);
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
            padding: 20px;
            @include border-radius(5px);
            >.comment {
                margin: 5px 5px 15px;
                border: 1px solid rgba(0, 0, 0, 0.2);
                @include border-radius(5px);
                >.inf {
                    background-color: $color_primary;
                    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
                    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
                    padding: 0 5px;
                    >span {
                        display: block;
                        color: #fff;
                        &.name {
                            color: #fff;
                        }
                        &.date {
                            font-size: xx-small;
                        }
                        >i {
                            color: #fff;
                        }
                    }
                }
                >.cont {
                    padding: 5px;
                }
            }
            >.no-comment {
                margin: 5px 5px 15px;
            }
            >.message_add_coms {
                width: 100%;
                text-align: center;
            }
            >.add_com {
                margin: 5px 5px 15px;
                padding: 5px;
                >.group-inline {
                    @include container();
                    >label {
                        @include span(first 1);
                        padding: $base-spacing / 4;
                    }
                    >input {
                        @include span(last 7);
                    }
                    >textarea {
                        @include span(last 7);
                        min-height: 200px;
                    }
                }
                >button {
                    margin-left: auto;
                    float: right;
                }
            }
        }
        .page_content {
            $a: span(8);
            $b: 20px;
            float: left;
            width: -webkit-calc(#{$a} - #{$b});
            width: -moz-calc(#{$a} - #{$b});
            width: calc(#{$a} - #{$b});
            margin: 10px;
            padding: 15px;
            -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .2);
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .2);
        }
        .tabsnavtitle {
            overflow: hidden;
            border: 1px solid #ccc;
            background-color: #f1f1f1;
            margin-top: 1%;

            > button {
                background-color: inherit;
                float: left;
                border: none;
                outline: none;
                cursor: pointer;
                padding: 14px 16px;
                transition: 0.3s;
                font-size: 17px;
                &:hover {
                    background-color: #ddd;
                }
                &.active {
                    background-color: #ccc;
                }
            }
        }
        .tabsnavcontent {
            .tabsc {
                display: none;
                -webkit-animation: fadeEffect 1s;
                animation: fadeEffect 1s;
            }
            .show {
                display: block;
            }
        }
    }
    >footer {
        @include span(8);
        bottom: 0;
        @media only screen and (max-width: 40rem) {
            height: 70px;
        }
        height: 30px;
        overflow: auto;
        background-color: $color_primary;
        color: #fff;
        flex: none;
        font-size: small;
        text-align: center;
        padding: 5px;
        margin-top: 10px;
        bottom: 0;
    }
    >.scroll-top-wrapper {
        position: fixed;
        opacity: 0;
        visibility: hidden;
        overflow: hidden;
        text-align: center;
        z-index: 99999999;
        background-color: #777777;
        color: #eeeeee;
        width: 50px;
        height: 48px;
        line-height: 48px;
        right: 30px;
        bottom: 30px;
        padding-top: 2px;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        -webkit-transition: all 0.5s ease-in-out;
        -moz-transition: all 0.5s ease-in-out;
        -ms-transition: all 0.5s ease-in-out;
        -o-transition: all 0.5s ease-in-out;
        transition: all 0.5s ease-in-out;
        &:hover {
            background-color: #888888;
        }
        &.show {
            visibility:visible;
            cursor:pointer;
            opacity: 1.0;
        }
        i.fa {
             line-height: inherit;
        }
    }
}

.recherche_modal {
    display: none;
    position: fixed;
    z-index: 1;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.4);
    >.modal {
        width: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        border: 1px solid rgba(0, 0, 0, .2);
        -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .2);
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .2);
        @include border-radius(5px);
        >.titre {
            background-color: $color_primary;
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
            padding: 5px 10px;
            font-size: 38px;
            font-weight: 300;
            line-height: 1.42;
            margin: 0;
            text-align: center;
        }
        >.content {
            background-color: #ffffff;
            padding: 10px 5px 5px;
            >form {
                >.group-inline {
                    @include container();
                    >label {
                        @include span(first 2);
                        padding: $base-spacing / 4;
                    }
                    >input {
                        @include span(last 6);
                    }
                }
                >.btn_act {
                    >span {
                        float: right;
                        padding: 0.75em 1.5em;
                        cursor: pointer;
                        >i {
                            color: red;
                        }
                    }
                    >button {
                        float: right;
                        @include clearfix;
                        margin: 5px 0 10px;
                    }
                }
            }
        }
    }
}

#errors {
    $a: span(8);
    $b: 20px;
    float: left;
    width: -webkit-calc(#{$a} - #{$b});
    width: -moz-calc(#{$a} - #{$b});
    width: calc(#{$a} - #{$b});
    margin: 10px;
    padding: 15px;
    -webkit-box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .2);
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, .2);
    >h1 {
        padding-bottom: 10px;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    }
    .description {
        padding-top: 15px;
        >a {
            @include a_href(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
        }
    }
}

.eupopup-container {
    background-color: rgba(25, 25, 25, 0.9);
    color: #efefef;
    padding: 5px 20px;
    font-size: 12px;
    line-height: 1.2em;
    text-align: center;
    display: none;
    z-index: 9999999;
    &.eupopup-container-bottom {
        position: fixed;
        bottom: 0;
        left: 0;
        right: 0;
    }
    >.eupopup-head {
        font-size: 1.2em;
        font-weight: bold;
        padding: 7px;
        color: #fff;
    }
    >.eupopup-body {
        color: #a2a2a2;
    }
    >.eupopup-buttons {
        padding: 7px 0 5px;
        >.eupopup-button {
            margin: 0 10px;
            &.eupopup-button_1 {
                color: #f6a21d;
                font-weight: bold;
                font-size: 12px;
                cursor: pointer;
            }
            &.eupopup-button_2 {
                color: #f6a21d;
                font-weight: normal;
                font-size: 12px;
            }
            &:focus,
            &:hover {
                text-decoration: underline;
                color: #f6a21d;
            }
        }
    }
}

.article_bloc {
    $a: span(2);
    $b: 10px;
    float: left;
    width: -webkit-calc(#{$a} - #{$b});
    width: -moz-calc(#{$a} - #{$b});
    width: calc(#{$a} - #{$b});
    margin: 5px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
    @include border-radius(5px);
    @media only screen and (max-width: 64rem) {
        $a: span(4);
        $b: 10px;
        float: left;
        width: -webkit-calc(#{$a} - #{$b});
        width: -moz-calc(#{$a} - #{$b});
        width: calc(#{$a} - #{$b});
        margin: 5px;
    }
    @media only screen and (max-width: 40rem) {
        $a: span(8);
        $b: 10px;
        float: left;
        width: -webkit-calc(#{$a} - #{$b});
        width: -moz-calc(#{$a} - #{$b});
        width: calc(#{$a} - #{$b});
        margin: 5px;
    }
    >.images {
        >a {
            >img {
                width: 100%;
                max-height: 250px;
                @include border-radii(5px, 5px, 0px, 0px);
            }
        }
        >.tags {
            margin-top: -33px;
            position: absolute;
            >a {
                color: #fff;
                text-decoration: none;
                background-color: $color_primary;
                padding: 5px;
                @include after-first(1) {
                    border-left: 1px solid #fff;
                }
            }
        }
    }
    >.info {
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        padding: 0 5px 5px;
        color: rgba(0, 0, 0, 0.2);
        @include container();
        >.publish_date {
            @include span(first 6);
            >span {
                color: #000;
            }
        }
        >.coms {
            @include span(last 2);
            text-align: right;
            >span {
                color: #000;
            }
        }
    }
    >a {
        color: #333;
        text-decoration: none;
        >h1 {
            margin-top: 5px;
            margin-bottom: 0;
            text-align: center;
        }
    }
    >.content {
        min-height: 300px;
        max-height: 300px;
        height: 300px;
        padding: 5px;
        position: relative;
        word-wrap: break-word;
        overflow: hidden;
        text-align: justify;
        a {
            @include a_href(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
        }
    }
    >.read_more {
        border-top: 1px solid rgba(0, 0, 0, 0.2);
        padding: 5px 15px 30px 5px;
        >a {
            float: right;
            @include a_href(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
        }
    }
}

.btn {
    @include border-radius(3px);
    @include button-color($color_primary);
    padding: .5em;
    margin-bottom: 1em;
    color: rgb(255, 255, 255);
    transition: background .15s;
    box-shadow: inset 0 1px rgba(255, 255, 255, .2);
    border: if($btn-border != false, 1px solid, none);

    &:disabled {
        cursor: not-allowed;
        opacity: 0.5;
        &:hover {
            background-color: $color_primary;
        }
    }
}

.btn-social {
    position: relative;
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: .5em;
    display: block;
    @include border-radius(3px);
    margin-bottom: 2%;
    &.btn-facebook {
        @include button-variant(#ffffff, #3b5998, rgba(0,0,0,.2));
    }
    &.btn-twitter {
        @include button-variant(#ffffff, #55acee, rgba(0,0,0,.2));
    }
    &.btn-google {
        @include button-variant(#ffffff, #dd4b39, rgba(0,0,0,.2));
    }
    &.btn-reddit {
        @include button-variant(#000000, #eff7ff, rgba(0,0,0,.2));
        border: 1px solid #000000;
    }
}

/* Fade in tabs */
@-webkit-keyframes fadeEffect {
    from {opacity: 0;}
    to {opacity: 1;}
}

@keyframes fadeEffect {
    from {opacity: 0;}
    to {opacity: 1;}
}