@mixin a_href ($c: $a_color, $hc: $a_hover_color) {
    color: $c!important;
    text-decoration: none;
    display: inline-block;
    position: relative;
    padding-bottom: 3px;

    &.active {
        &:after {
            height: 3px;
            display: block;
            width: 100%;
            background: $hc;
            content: '';
        }

        &:before {
            display: block;
            content: '';
            background: silver;
            height: 3px;
            margin-top: -20px;
        }
    }

    &:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        height: 3px;
        width: 0;
        transition: width 0s ease, background 0.5s ease;
    }

    &:after {
        content: '';
        display: block;
        position: absolute;
        right: 0;
        bottom: 0;
        height: 3px;
        width: 0;
        background: $hc;
        transition: width 0.5s ease;
    }

    &:hover {
        color: $hc;

        &:before {
            width: 100%;
            background: $hc;
            transition: width 0.5s ease;
        }

        &:after {
            width: 100%;
            background: transparent;
            transition: all 0s ease;
        }
    }
}